import i18n from 'i18next'
import { initReactI18next } from 'react-i18next'
import enTranslation from '../src/assets/locales/en-US/translation.json'
import ruTranslation from '../src/assets/locales/ru-RU/translation.json'

i18n.use(initReactI18next).init({
  resources: {
    en: {
      translation: enTranslation,
    },
    ru: {
      translation: ruTranslation,
    },
  },
  lng: 'ru',
  fallbackLng: 'ru',
  debug: true,
  interpolation: {
    escapeValue: false,
  },
})

export default i18n
